header {
  display: flex;
  align-items: center;
  margin-top: 3%;
  height: 90%;
  width: 100%;
}

header h2 {
  color: var(--font-color);
  margin-top: 10%;
  font-weight: 600;
}
header h1 {
  margin-top: 10%;
  font-size: 3rem;
  color: var(--font-color);
  line-height: 45px;
  letter-spacing: 2px;
  text-shadow: 1px 1px 0 #ffcd4e;
}

#cadou {
  margin-bottom: 6%;
  height: 80%;
  width: 45%;
  margin-left: auto;
  background-image: url(./imagini/cadou.png);
  background-position: right;
  background-size: contain;
  background-repeat: no-repeat;
  z-index: -1;
}

.title-container {
  width: 40%;
  height: 100%;
  padding-left: 7%;
  padding-right: 7%;
}

a {
  text-decoration: none;
  color: var(--font-color);
}

button {
  margin-top: 7%;
  align-items: center;
  background-image: linear-gradient(144deg, #af40ff, #5b42f3 50%, #00ddeb);
  border: 0;
  border-radius: 8px;
  box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  color: #ffffff;
  display: flex;
  font-family: Phantomsans, sans-serif;
  font-size: 18px;
  justify-content: center;
  line-height: 1em;
  max-width: 100%;
  min-width: 140px;
  padding: 3px;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  cursor: pointer;
  transition: all 0.3s;
}

button:active,
button:hover {
  outline: 0;
}

button span {
  background-color: rgb(5, 6, 45);
  padding: 16px 24px;
  border-radius: 6px;
  width: 100%;
  height: 100%;
  transition: 300ms;
}

button:hover span {
  background: none;
}

button:active {
  transform: scale(0.9);
}

@media screen and (max-width: 1120px) {
  header h2 {
    font-size: 1.3rem;
  }
  header h1 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 780px) {
  header {
    flex-direction: column;
    height: 75%;
  }

  header h2 {
    font-size: 1rem;
    margin-top: 4%;
  }
  header h1 {
    font-size: 1.5rem;
    margin-top: 4%;
    line-height: 25px;
  }

  .title-container {
    margin-top: 7%;
    width: 100%;
    height: 50%;
  }

  #cadou {
    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0;
    background-position: bottom right;
    width: 100%;
    height: 40%;
  }
}
