nav {
  width: 100%;
  height: 55px;
  border-bottom: 5px solid var(--font-color);
  border-radius: 50%;
  display: flex;
  justify-content: center;
}

#nav--ul {
  list-style-type: none;
  color: var(--font-color);
  font-size: 1.5rem;
  color: var(--c1);
  text-shadow: 1px 1px 0 rgb(68, 64, 64);
  font-weight: 600;
  letter-spacing: 4px;
}
